.bg-tree-container {
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: hidden;
    pointer-events: none;
    z-index: 0; }

.bg-tree {
    position: absolute;
    opacity: 0.15;
    transition: opacity 0.5s;
    user-select: none; }

.fade {
    opacity: 0; }


.bg-tree:nth-child(1) {
    width: 100px;
    transform: rotate(20deg);
    top: 20vh;
    left: 80vw; }

.bg-tree:nth-child(2) {
    width: 120px;
    transform: rotate(40deg);
    top: 30vh;
    left: 20vw; }

.bg-tree:nth-child(3) {
    width: 180px;
    transform: rotate(80deg);
    top: 70vh;
    left: 50vw; }

.bg-tree:nth-child(4) {
    width: 210px;
    transform: rotate(120deg);
    top: 30vh;
    left: 60vw; }

.bg-tree:nth-child(5) {
    width: 220px;
    transform: rotate(220deg);
    top: 60vh;
    left: 30vw; }

.bg-tree:nth-child(6) {
    width: 170px;
    transform: rotate(150deg);
    top: 77vh;
    left: 80vw; }

.bg-tree:nth-child(7) {
    width: 140px;
    transform: rotate(300deg);
    top: 10vh;
    left: 20vw; }

.bg-tree:nth-child(8) {
    width: 160px;
    transform: rotate(600deg);
    top: 20vh;
    left: 50vw; }

.bg-tree:nth-child(9) {
    width: 190px;
    transform: rotate(240deg);
    top: 70vh;
    left: 10vw; }

.bg-tree:nth-child(10) {
    width: 120px;
    transform: rotate(20deg);
    top: 55vh;
    left: 15vw; }

.bg-tree:nth-child(11) {
    width: 150px;
    transform: rotate(40deg);
    top: 35vh;
    left: 25vw; }

.bg-tree:nth-child(12) {
    width: 190px;
    transform: rotate(80deg);
    top: 75vh;
    left: 22vw; }

.bg-tree:nth-child(13) {
    width: 170px;
    transform: rotate(120deg);
    top: 75vh;
    left: 70vw; }

.bg-tree:nth-child(14) {
    width: 200px;
    transform: rotate(220deg);
    top: 17vh;
    left: 65vw; }

.bg-tree:nth-child(15) {
    width: 220px;
    transform: rotate(150deg);
    top: 17vh;
    left: 30vw; }

.bg-tree:nth-child(16) {
    width: 160px;
    transform: rotate(300deg);
    top: 10vh;
    left: 10vw; }

.bg-tree:nth-child(17) {
    width: 130px;
    transform: rotate(600deg);
    top: 35vh;
    left: 5vw; }

.bg-tree:nth-child(18) {
    width: 100px;
    transform: rotate(240deg);
    top: 50vh;
    left: 80vw; }

@media (max-width: 480px) {
  .bg-tree:nth-child(8),
  .bg-tree:nth-child(3),
  .bg-tree:nth-child(5),
  .bg-tree:nth-child(7),
  .bg-tree:nth-child(15),
  .bg-tree:nth-child(13),
  .bg-tree:nth-child(17) {
    display: none; } }
